#navbar {
  position: absolute;
  min-width: 96vw;
  min-height: 80px;
  bottom: 1%;
  margin: 0 2vw;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  opacity: 0;
  transition: 1s;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.navHolder {
  width: 20%;
  /* background-color: aqua; */
  position: absolute;
  height: 180%;
  bottom: 0;
  z-index: -1;
}

.nameHolder {
  z-index: 1;
  background-color: aqua;
}

.NameTag {
  position: absolute;
  min-width: 140px;
  min-height: 60px;
  border: solid 2px rgba(138, 138, 138, 0.614);
  border-radius: 10px;
  bottom: 80px;
  left: 80px;
  background-color: rgba(209, 209, 209, 0.833);
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0s;
  font-size: 20px;
  box-shadow: 0px 0px 5px 0px grey;
}

.MText {
  font-size: 40px;
  margin: 0;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  transition: 0.3s;
  -webkit-user-select: none;
          user-select: none;
}

/* rgba(253, 108, 108, 0.795) */

.NameTag h1 {
  margin: 0 20px;
  color: rgb(23, 23, 59);
  transition: 0.1s;
}

.Menu {
  background-color: rgba(216, 216, 216, 0.91);
  border: solid 2px rgb(169, 169, 169);
  width: 60px;
  height: 60px;
  position: absolute;
  top: -62px;
  left: 0%;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  transition: 0.1s;
  box-shadow: 0px 0px 5px 0px grey;
  cursor: pointer;
  z-index: 3;
}

.Menu:hover{
  background-color: azure;
}

.Menu:active {
  box-shadow: 0px 0px 2px 0px rgb(60, 60, 60);
  border: solid 2px rgba(253, 108, 108, 0.795);
  transition: 0.1s;
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

.MenuTab {
  display: flex;
  min-width: 10%;
  min-height: 60px;
  margin: 0;
  justify-content: space-around;
  align-items: center;
  padding: 0;
  opacity: 0;
  transition: 0.3s;
  border-radius: 0 15px 15px 15px;
  border: solid 2px rgba(183, 183, 183, 0.795);
  background-color: rgba(151, 151, 151, 0.874);
  cursor: pointer;
  box-shadow: 0px 0px 5px 2px rgba(255, 255, 255, 0.64);
}

.homebtn{
  bottom: 80px;
  left: 80px;
  position: absolute;
  width: 60px;
  min-height: 60px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  border: solid 2px rgba(183, 183, 183, 0.795);
  background-color: rgba(209, 209, 209, 0.833);
  font-size: 20px;
  box-shadow: 0px 0px 5px 0px grey;
  border: solid 2px rgba(138, 138, 138, 0.614);
  border-radius: 10px;
  color: rgb(23, 23, 59);
  opacity: 0;
  cursor: pointer;
}

.homebtn:hover{
  background-color: azure;
}

.meuText {
  margin: 0 20px;
  color: rgb(45, 46, 79);
