}

.navBtns:active {
  background-color: rgb(255, 255, 255);
  transition: 0.2s;
  box-shadow: 0 0 0px 0px rgb(32, 32, 32);
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}


