.tableHolder {
  background-color: var(--worldcolor);
  width: 87vw;
  height: 85vh;
  border-radius: 0 0 10px 10px;
  overflow: scroll;
}

table {
  background-color: blueviolet;
  width: 87vw;
  border-collapse: collapse;
  display: table;
  max-height: 100%;
}

table tr {
  display: table-row;
  cursor: pointer;
  font-weight: 500;
  height: 35px;
}

table thead {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

table td {
  display: table-cell;
  padding: 1px;
  text-align: end;
  /* border: 1px dotted rgb(124, 124, 1 24); */
  /* padding: 8px 3px; */
  border-right: double 1px #dedede; 
  /* border-left: solid 1px #f00; */
  font-size: 18.5px;
}

tr:hover {
  background-color: rgb(255, 228, 187);
    font-weight: 600;

}

th {
  background-color: #0c0a16;
  color: rgb(192, 192, 192);
  text-align: center;
  font-size: 17px;
  /* font-weight: normal; */
  padding: 3px 2px;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

.tableRow {
  background-color: #eaeaff;
}

.tableRow td:nth-child(1){
  /* height: 100%; */
  display: flex;
  align-items: center;
  justify-content: center;
}

.tableFlag {
  background-image: url(/covid_tracker/static/media/worldFlag.69d3f494.png);
  width: 25px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  height: 35px;
  padding: 0 10px;
}

.tableRow td:nth-child(2) {
  text-align: left;
  padding: 5px 0 5px 15px;
  width: 240px;
  
}

tbody .tableRow:nth-child(1){
font-weight: bold;
background-color: #dfdfdf;
}

tbody .tableRow:nth-child(even) {
  background-color: white;
}

tbody .tableRow:nth-child(even):hover {
  background-color: rgb(255, 228, 187);
}

